<template>
	<article>
		<section>
			<div class="search">

				<el-input placeholder="文章名/标签名" icon="search" v-model="searchInput" :on-icon-click="handleIconClick">
				</el-input>
			</div>
			<div class="tag-classify">
				标签&nbsp;&nbsp;:&nbsp;&nbsp;
				<span class="active" v-on:click="getArticlesList">全部</span>
				<span v-for="tag in tags" v-bind:class="tag.Class" v-on:click="getArticlesListBytagId(tag.id)">
					{{tag.name}}
				</span>
			</div>
		</section>

		<section>
			<el-row :gutter="20">
				<el-col :span="16">
					<connent v-bind:articlesList="articlesList" v-on:laodMore="laodMore"></connent>
				</el-col>
				<el-col :span="8">
					<aside>
						<home-aside></home-aside>
					</aside>
				</el-col>
			</el-row>
		</section>
	</article>
</template>
<script>
	import { Row, Col, Input } from 'element-ui'
	import Content from './Content.vue'
	import Aside from '../public/Aside.vue'
	export default {
		name: 'articles',
		components: {
			'el-row': Row,
			'el-col': Col,
			'connent': Content,
			'home-aside': Aside,
			'el-input': Input,
		},
		data() {
			return {
				tags: [],
				articlesList: [],
				searchText: "",
				page: 0,
				rows: 10,
				isGetAll: true,
				tag_id: '',
			};
		},

		methods: {
			handleIconClick() {

			},
			getAllTags() {
				this.$http.get('/tag/getAll').then((response) => {
					//console.log(response.data);
					this.tags = response.data;
				}, (response) => {
					console.log("获取所有标签失败");
				});
			},
			getArticlesList() {
				this.isGetAll = true;
				this.$http.get("/article/getArticlesForPage?page=" + this.page + "&size=" + this.rows).then((response) => {
					console.log(response.data);
					this.articlesList = response.data.content;
					for(let i = 0; i < this.articlesList.length; i++) {
						(function(self, articles) {
							console.log(articles[i]);
							self.$http.get("/article/getTags/" + articles[i].id).then((response) => {
								console.log(response.data);
								articles[i].tagSet = response.data;
							}, (response) => {
								console.log("获取文章标签失败");
								/*articles[i].tagSet = [{
									"name": "失败测试"
								}];*/
							});
						})(this, this.articlesList);
					}
				}, (response) => {
					this.$message.error('获取文章失败');
				});
			},
			getArticlesListBytagId(tag_id) {
				this.isGetAll = true;
				this.tag_id = tag_id;
				this.$http.get("/article/getArticlesForPage?page=" + this.page + "&size=" + this.rows + "&tid=" + tag_id).then((response) => {
					console.log(response.data);
					this.articlesList = response.data.content;
					for(let i = 0; i < this.articlesList.length; i++) {
						(function(self, articles) {
							console.log(articles[i]);
							self.$http.get("/article/getTags/" + articles[i].id).then((response) => {
								console.log(response.data);
								articles[i].tagSet = response.data;
							}, (response) => {
								console.log("获取文章标签失败");
								/*articles[i].tagSet = [{
									"name": "失败测试"
								}];*/
							});
						})(this, this.articlesList);
					}
				}, (response) => {
					this.$message.error('获取文章失败');
				});
			},
			laodMore() {
				this.rows += 10;
				this.getArticlesList();

			},
		},
		created() {
			this.getAllTags();
			if(this.isGetAll) {
				this.getArticlesList();
			} else {
				getArticlesListBytagId(this.tag_id)
			}

		}
	}
</script>
<style scoped>
	.el-row {
		margin: 10px 0 20px 0 !important;
	}
	
	.el-col {
		border-radius: 4px;
		padding-left: 0 !important;
		padding-right: 0 !important;
	}
	
	article {
		min-width: 800px;
		max-width: 960px;
		margin: 30px auto 20px;
	}
	
	.search {
		width: 210px;
	}
	
	.tag-classify {
		margin-top: 15px;
		font-size: 14px;
		font-weight: 800;
	}
	
	.tag-classify span {
		display: inline-block;
		cursor: pointer;
		font-family: "微软雅黑";
		margin: 0 10px 10px 0;
		padding: 5px 10px;
		font-weight: 400;
		border-radius: 3px;
	}
	
	.tag-classify span:hover {
		color: red;
	}
	
	.tag-classify span.active {
		color: #fff;
		background-color: #2b333b;
	}
</style>


